.menu {
    display: grid;
    grid-row-gap: 16px;
    text-align: start;
    background: transparent;
    color: #5F6368;
    position: relative;
    top: 10px;
    left: 10px;
    max-width: 150px;
}

.menu__item {
    border-left: 5px solid #DADCE0;
    padding: 7px 0 7px 13px;
    cursor: pointer;
    transition: all .2s linear;
}

.menu__item:hover {
    color: #185ABF;
    border-left: 5px solid #185ABF;
}
